import { Col, Table, Row, Space, Button, message, Menu, Layout } from "antd"
import Search from "antd/lib/input/Search"
import { Content } from "antd/lib/layout/layout"
import Sider from "antd/lib/layout/Sider"
import Column from "antd/lib/table/Column"
import { useState, useEffect } from "react"
import booksService from '../services/books'

const Books = () => {
    const [loading, setLoading] = useState(true)
    const [books, setBooks] = useState([])
    useEffect(() => {
        setLoading(true)
        booksService
            .getBooks()
            .then(response => {
                if (Number(response) === -1) {
                    message.error('')
                } else {
                    response = JSON.parse(response)
                    setBooks(response)
                }
                setLoading(false)
            })
            .catch(error => {
                message.error('')
                console.log(error)
                setLoading(false)
            })
    }, [])

    const [types, setTypes] = useState([])
    useEffect(() => {
        booksService
            .getTypes()
            .then(response => {
                if (Number(response) === -1) {
                    message.error('')
                } else {
                    response = JSON.parse(response)
                    setTypes(response)
                }
            })
            .catch(error => {
                message.error('')
                console.log(error)
            })
    }, [])

    const [filter, setFilter] = useState('')
    const [filterBooks, setFilterBooks] = useState([])
    useEffect(() => {
        setFilterBooks(books)
        setLoading(false)
    }, [books])

    const selectType = (key) => {
        setLoading(true)
        setFilter('')
        if (key === 0) {
            setFilterBooks(books)
        } else {
            let newBooks = []
            books.forEach(book => {
                if (book.bookTypeId === key) {
                    newBooks = newBooks.concat(book)
                }
            })
            setFilterBooks(newBooks)
        }
        setLoading(false)
    }

    const onChange = (event) => {
        setFilter(event.target.value)
    }

    const onSearch = () => {
        if (filter === '') {
            setFilterBooks(books)
        } else {
            let newBooks = []
            books.forEach(book => {
                if (book.bookName.indexOf(filter) !== -1) {
                    newBooks = newBooks.concat(book)
                }
            })
            setFilterBooks(newBooks)
        }
    }

    const reserve = (record) => {
        booksService.reserve(record.bookIsbn).then(response => {
            if (Number(response) === -1) {
                message.error('')
            } else {
                message.success(`${Number(response)}`)
            }
        }).catch(error => {
            message.error('')
            console.log(error);
        })
    }

    return (
        <div>
            <Sider width={200} className="site-layout-background" style={{ float: 'left' }}>
                <Menu mode="inline" defaultSelectedKeys={['0']} style={{ hight: '100%', borderRight: 0 }}>
                    <Menu.Item key={0} onClick={() => { selectType(0) }}></Menu.Item>
                    {
                        types.map(type => {
                            return (
                                <Menu.Item key={type.bookTypeId} onClick={() => { selectType(type.bookTypeId) }}>{type.bookType}</Menu.Item>
                            )
                        })
                    }
                </Menu>
            </Sider>
            <Layout style={{ padding: '0 24px 24px' }}>
                <Row>
                    <Col>
                        <Button></Button>
                    </Col>
                    <Col span={4} offset={18}>
                        <Search placeholder="" value={filter} onChange={onChange} onSearch={onSearch} style={{ margin: '16px auto' }} />
                    </Col>
                </Row>
                <Content className="site-layout-background" style={{
                    padding: 24,
                    margin: 0,
                    minHeight: 280
                }}>
                    <Table dataSource={filterBooks} loading={loading} pagination={{ defaultCurrent: 1, total: filterBooks.length }} rowKey={(record) => { return record.bookIsbn }}>
                        <rowSelection />
                        <Column title='' dataIndex='bookName' key='bookName' />
                        <Column title='' dataIndex='bookAuthor' key='bookAuthor' />
                        <Column title='ISBN' dataIndex='bookIsbn' key='bookIsbn' />
                        <Column title='' dataIndex='bookMoney' key='bookMoney' />
                        <Column title='' dataIndex='bookPublictime' key='bookPublictime' />
                        <Column title='' dataIndex='bookPress' key='bookPress' />
                        <Column title='' dataIndex='bookType' key='bookType' />
                        <Column title='' dataIndex='count' key='count' />
                        <Column title='' key='action' render={(text, record) => (
                            <Space>
                                <Button onClick={() => { reserve(record) }}></Button>
                            </Space>
                        )} />
                    </Table>
                </Content>
            </Layout>
        </div>
    )
}

export default Books